@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*:before,
*:after {
  box-sizing: border-box; }

:focus,
:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

body {
  overflow-x: hidden;
  background-color: #EFF7ED;
  font-family: 'Roboto'; }

nav,
footer,
header,
aside {
  display: block; }

input,
button,
textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

ul {
  list-style: none; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit; }

/* Header */
.header {
  background: linear-gradient(0deg, #FFFFFF, #FFFFFF);
  box-shadow: inset 0px 1px 0px rgba(28, 175, 94, 0.1);
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header__logo {
    background: #3BC58C;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 240px;
    padding: 20px 0; }
  .header__icons {
    display: flex;
    padding: 20px 30px 20px 30px; }
    .header__icons--notification {
      position: relative;
      margin-right: 30px; }
      .header__icons--notification svg {
        cursor: pointer; }
        .header__icons--notification svg:last-child {
          margin-right: 0; }
      .header__icons--notification span {
        position: absolute;
        top: -7px;
        left: 9px;
        background: #1DCE6C;
        width: 17px;
        height: 17px;
        color: #fff;
        text-align: center;
        border-radius: 50%;
        font-style: normal;
        font-weight: bold;
        font-size: 10px;
        display: flex;
        justify-content: center;
        align-items: center; }
    .header__icons--search svg {
      cursor: pointer; }

/* Sidebar */
.sidebar {
  background: linear-gradient(180deg, #2DBC80 0.55%, #53CC71 99.33%);
  box-shadow: inset -1px 0px 0px rgba(28, 175, 94, 0.1);
  height: 100vh;
  min-width: 240px;
  margin-top: -1px; }
  .sidebar__avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 40px; }
    .sidebar__avatar img {
      margin-right: 10px; }
    .sidebar__avatar--name {
      font-size: 14px;
      color: #fff; }
      .sidebar__avatar--name svg {
        cursor: pointer;
        margin-left: 5px; }
    .sidebar__avatar--num {
      font-size: 12px;
      font-weight: bold;
      margin-top: 5px;
      color: rgba(0, 0, 0, 0.3); }
  .sidebar__info {
    padding: 40px; }
    .sidebar__info ul {
      text-align: left; }
      .sidebar__info ul li {
        padding: 15px 0;
        cursor: pointer; }
        .sidebar__info ul li:hover {
          background: rgba(255, 255, 255, 0.15);
          box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.0472713);
          border-radius: 5px; }
        .sidebar__info ul li a {
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          display: flex;
          align-items: center;
          color: #fff; }
          .sidebar__info ul li a svg {
            margin: 0 15px 0 20px; }
  .sidebar__nav {
    padding: 375px 35px 30px 35px; }
    .sidebar__nav ul {
      display: flex;
      justify-content: center; }
      .sidebar__nav ul:first-child {
        padding: 20px 0 10px 0;
        border-top: 1px solid rgba(0, 0, 0, 0.07); }
      .sidebar__nav ul li {
        margin-right: 25px; }
        .sidebar__nav ul li:last-child {
          margin-right: 0; }
        .sidebar__nav ul li a {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 14px;
          color: #147A42; }
          .sidebar__nav ul li a:hover {
            color: #fff; }

/* Main */
.main {
  display: flex; }

.info {
  padding: 68px 20px 130px 20px;
  margin: 0 auto;
  width: 990px; }
  .info__head {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .info__head a {
      font-weight: bold;
      font-size: 24px;
      line-height: 28px;
      text-transform: lowercase;
      color: #1CAF5E; }
      .info__head a:hover {
        text-decoration: underline; }
    .info__head button {
      font-weight: normal;
      padding: 11px 16px;
      font-size: 14px;
      line-height: 16px;
      color: #1CAF5E;
      background: transparent;
      border: 1px solid #1CAF5E;
      border-radius: 4px; }
      .info__head button:hover {
