  .info__day {
    padding-top: 20px; }
    .info__day a {
      font-weight: normal;
      font-size: 16px;
      color: #333333;
      margin-right: 35px;
      position: relative; }
      .info__day a:last-child {
        margin-right: 0; }
      .info__day a::after {
        content: '';
        position: absolute;
        bottom: -8px;
        left: 0;
        border-radius: 5px;
        border-bottom: 4px solid #B8E986;
        width: 0%; }
      .info__day a:hover::after {
