    .info__item--linkedin {
      background: linear-gradient(180deg, #007EBB 0%, #007E9D 98.51%), #007EBB;
      box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.03);
      border-radius: 10px; }
      .info__item--linkedin:hover {
        -webkit-box-shadow: 1px 1px 8px 2px #007EB7;
        box-shadow: 1px 1px 8px 2px #007EB7; }
    .info__item--youtube {
      background: linear-gradient(180deg, #CE1312 0%, #B01312 100%), #CE1312;
      box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.03);
      border-radius: 10px; }
      .info__item--youtube:hover {
        -webkit-box-shadow: 1px 1px 8px 2px #C91413;
        box-shadow: 1px 1px 8px 2px #C91413; }
    .info__item--snapchat {
      background: linear-gradient(180deg, #FFFE00 0%, #F3F200 96.43%), #FFFE00;
      box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.03);
      border-radius: 10px; }
      .info__item--snapchat:hover {
        -webkit-box-shadow: 1px 1px 8px 2px #c9c909;
        box-shadow: 1px 1px 8px 2px #c9c909; }
    .info__item--google {
      background: linear-gradient(180deg, #F93F2D 0%, #C73F2D 100%), #F93F2D;
      box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.03);
      border-radius: 10px; }
      .info__item--google:hover {
        -webkit-box-shadow: 1px 1px 8px 2px #E43E2D;
        box-shadow: 1px 1px 8px 2px #E43E2D; }
    .info__item--vine {
      background: linear-gradient(180deg, #00B687 0%, #008E87 100%), #00B687;
      box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.03);
      border-radius: 10px; }
      .info__item--vine:hover {
        -webkit-box-shadow: 1px 1px 8px 2px #009D87;
        box-shadow: 1px 1px 8px 2px #009D87; }
    .info__item--text {
      font-family: 'Oswald'; }
      .info__item--text h1 {
        font-weight: bold;
        font-size: 42px;
        text-transform: uppercase;
        padding-top: 20px;
        color: #FFFFFF; }
      .info__item--text p {
        font-weight: normal;
        font-size: 11px;
        letter-spacing: 2.5px;
        text-transform: uppercase;
        color: #FFFFFF; }
    .info__item--textBlack {
      font-family: 'Oswald'; }
      .info__item--textBlack h1 {
        font-weight: bold;
        font-size: 42px;
        text-transform: uppercase;
        padding-top: 20px;
        color: black; }
      .info__item--textBlack p {
        font-weight: normal;
        font-size: 11px;
        letter-spacing: 2.5px;
        text-transform: uppercase;
        color: black; }
    .info__item--sum {
      font-weight: normal;
      font-size: 26px;
      line-height: 30px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      color: #FFFFFF;
      position: absolute;
      right: 10px;
      bottom: 10px; }
      .info__item--sum svg {
        margin-left: 5px; }
  .info__user {
    margin-top: 10px;
    display: flex;
    align-items: center; }
    .info__user--link {
      margin-left: 10px; }
      .info__user--link p {
