/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1001;
  opacity: 0;
  visibility: hidden; }
  .modal.active {
    opacity: 1;
    visibility: visible;
    transition: all 0.5s ease; }
  .modal__dialog {
    padding: 20px 25px 30px 25px;
    margin: 115px auto;
    max-width: 600px;
    height: 260px;
    background-color: white; }
  .modal__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px; }
    .modal__title h2 {
      font-weight: bold;
      font-size: 18px;
      color: #4F4F4F; }
    .modal__title p {
      font-weight: normal;
      font-size: 14px;
      padding-top: 30px;
      color: #4F4F4F; }
  .modal__close--svg {
    cursor: pointer; }
  .modal__text {
    margin-bottom: 20px; }
  .modal__input {
    display: flex;
    flex-direction: column; }
    .modal__input label {
      font-weight: bold;
      font-size: 13px;
      color: #4F4F4F;
      margin-bottom: 10px; }
    .modal__input input {
      border: 1px solid #C5CFC9;
      font-weight: normal;
      font-size: 14px;
      color: #B6B6B6;
      border-radius: 6px;
      padding: 10px 15px; }
  .modal__btn {
    padding-top: 30px;
    display: flex;
    justify-content: flex-end; }
    .modal__btn button {
      width: 117px;
      height: 40px;
      background: linear-gradient(180deg, #53CD72 0%, #1CAF5E 100%);
      border-radius: 6px;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: #FFFFFF; }
      .modal__btn button:hover {
        background: linear-gradient(180deg, #3f9b56 0%, #1fd471 100%); }

@media (max-height: 880px) {
  .sidebar {
    height: auto; }
    .sidebar__nav {
      padding: 280px 35px 30px 35px; } }

@media (max-height: 790px) {
  .sidebar__nav {
    padding: 200px 35px 30px 35px; }
  .info {
    padding: 38px 20px 130px; } }

@media (max-width: 1080px) {
  .sidebar {
    height: auto; } }

@media (max-width: 768px) {
  .sidebar {
    margin-left: -180px;
    transition: all 0.5s ease;
    min-width: 180px; }
    .sidebar.active {
      margin-left: 0px; }
    .sidebar__info {
      padding: 10px; }
  .header__logo {
    width: 180px; }
  .header__burger {
    display: block;
    position: relative;
    width: 40px;
    height: 20px;
    z-index: 1001;
    cursor: pointer; }
    .header__burger span {
      position: absolute;
      background-color: #39C17B;
      width: 100%;
      height: 4px;
      left: 0;
      top: 8px;
      transition: 0.2s; }
    .header__burger::before, .header__burger::after {
      content: '';
      position: absolute;
      background-color: #39C17B;
      height: 4px;
      right: 0;
      transition: 0.2s; }
    .header__burger::before {
      top: 0;
      width: 40px; }
    .header__burger::after {
      bottom: 0;
      width: 40px; }
    .header__burger.active span {
      transform: scale(0); }
    .header__burger.active::before {
      transform: rotate(45deg);
      top: 8px;
      width: 25px;
      left: 15px;
      background-color: #39C17B; }
    .header__burger.active::after {
      transform: rotate(-45deg);
      bottom: 8px;
      width: 25px;
      background-color: #39C17B; }
  .info__card {
    margin-bottom: 10px; } }

@media (max-width: 530px) {
  .info__head {
    flex-direction: column; }
    .info__head a {
      margin-bottom: 10px; }
  .info__day a {
    margin-right: 10px; }
  .modal__dialog {
    max-width: 300px; }
  .modal__btn {
    justify-content: center; } }

@media (max-width: 440px) {
  .info__dashboard {
    justify-content: center; } }

@media (max-width: 380px) {
  .header__icons {
    padding: 20px 10px; }
    .header__icons--notification {
      margin-right: 20px; } }
